.Container {
display: flex;
flex-direction: column-reverse;
overflow-x: hidden;
}
.ToDosContainer{
  width: 100%;
  padding: 50px 0;
}

.todoCard {
    padding: 10px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.ToDosCreater{
  position: fixed;
  display: flex;
  bottom: 0;
  justify-content: center;
  width: 100%;
  height: auto;
  border-top: 1px solid #eee;
}

.ToDosInt{
width: 100%;
display: flex;
justify-content: space-between;
padding-left: 10px;
}
.inputField{
  width: 100%;
}
.sendBtn{
  background-color: #8b00dd !important;
}

.formContainer{
  width: 100%;
  padding: 15px;
  display: flex;
}

.deletBtn{
  position: fixed !important;
  visibility: hidden;
  bottom: 0;
  right: 0;
  margin-bottom: 70px !important;
  margin-right: 14px !important;
}

.bgChecked{
    background-color: rgba(59,255,69,0.3) !important;
}

.visisble{
    visibility: visible;
}
